export class Header extends Component {
    componentDidMount () {
        if ('serviceWorker' in navigator) {
        // navigator.serviceWorker
        //   .register(process.env.BACKEND_URL +'/service-worker.js')
        //   .then(registration => {
        //     console.log('service worker registration successful')
        //   })
        //   .catch(err => {
        //     console.warn('service worker registration failed', err.message)
        //   })
        }
        $(window).scroll(function() {
            $(window).scrollTop() > 0 && $(window).width() > 550 ? $("header").addClass("resize") : $("header").removeClass("resize")
        })
        
        $("header nav a").hover(function() {
            $("header navslider").css({
                left: $(this).position().left + parseInt($(this).css("marginLeft")) + "px",
                width: $(this).width() + "px",
                opacity: ".999"
            })
        }, function() {
            $("header navslider").css("opacity", "0")
        });
    }
    render () {
        return (
            <header>
                <logo><text>AVN SYSTEMS</text></logo>
                <nav>
                    <Link href='/' as={ process.env.BACKEND_URL + `/`}><a>Home</a></Link>
                    <Link href='/about' as={ process.env.BACKEND_URL + `/about`}><a>About</a></Link>
                    <Link href='/tvmaze' as={ process.env.BACKEND_URL + `/tvmaze`}><a>TV Maze</a></Link>
                    <Link href='/404' as={ process.env.BACKEND_URL + `/404`}><a>404</a></Link>
                </nav>
                <navtoggle><bar></bar><bar></bar><bar></bar></navtoggle>
                <navslider></navslider>
                <style jsx>{`
                    header {
                        overflow: hidden;
                        position: fixed;
                        z-index: 10000;
                        height: 90px;
                        width: 100%;
                    }
                    header logo {
                        position: absolute;
                        padding: 16px 0px;
                        width: 170px;
                        left: 90px;
                        top: 10px;
                        
                        background-image: url(./static/images/icon.png);
                        background-repeat: no-repeat;
                        background-size: contain;
                        filter: gray;
                        
                        -webkit-transition: all 150ms ease-in-out;
                        -moz-transition: all 150ms ease-in-out;
                        -ms-transition: all 150ms ease-in-out;
                        -o-transition: all 150ms ease-in-out;
                        transition: all 150ms ease-in-out;
                    }
                    header logo:hover {
                        -webkit-filter: none;
                        -moz-filter: none;
                        -o-filter: none;
                        filter: none;
                    }
                    header logo text {
                        display: -webkit-inline-box;
                        text-transform: uppercase;
                        letter-spacing: 1px;
                        position: relative;
                        font-weight: bold;
                        min-width: 110px;
                        font-size: 14px;
                        color: #5f5f5f;
                        left: 60px;
                    }
                    header nav {
                        margin: 0 20px;
                        float: right;
                    }
                    header nav a {
                        text-decoration: none;
                        display: inline-block;
                        text-transform: uppercase;
                        letter-spacing: 1px;
                        font-weight: bold;
                        margin: 25px 10px;
                        font-size: 14px;
                        color: #d9d9d9;
                    }
                    header nav a:hover {
                        color: #8500FF;
                    }
                    header navtoggle {
                        cursor: pointer;
                        display: inline-block;
                        position: absolute;
                        width: 45px;
                        left: 20px;
                        top: 15px;
                    }
                    header navtoggle bar {
                        border-bottom: 2px solid #aaa;
                        display: block;
                        margin: 7px 0;
                        width: 100%;
                        height: 5px;
                    }
                    header navtoggle:hover bar {
                        border-bottom: 2px solid #bc73ff;
                    }
                    header navslider {
                        border-bottom: 4px solid #8500FF;
                        position: absolute;
                        height: 0;
                        top: 50px;
                        
                        -webkit-transition: all 250ms ease-in-out;
                        -moz-transition: all 250ms ease-in-out;
                        -ms-transition: all 250ms ease-in-out;
                        -o-transition: all 250ms ease-in-out;
                        transition: all 250ms ease-in-out;
                    }
                    
                    @media (max-width: 625px) {
                        header {
                            background: white;
                            height: 58px;
                        }
                        header logo {
                            left: calc(50% - 85px);
                            -webkit-filter: none;
                            -moz-filter: none;
                            -o-filter: none;
                            filter: none;
                            top: 1px;
                        }
                        header nav {
                            display: none;
                        }
                        header navtoggle {
                            top: 5px;
                        }
                    }
                `}</style>
            </header>
        )
    }
}

